let touchStart = {x: 0, y: 0}
canvas.addEventListener('touchstart', e => {
    const touch = e.originalEvent?.touches[0] || e.originalEvent?.changedTouches[0] || e.touches[0] || e.changedTouches[0]
    touchStart.x = touch.clientX || touch.pageX
    touchStart.y = touch.clientY || touch.pageY
})

canvas.addEventListener('touchmove', e => {
    const touch = e.originalEvent?.touches[0] || e.originalEvent?.changedTouches[0] || e.touches[0] || e.changedTouches[0]
    const deltaX = touchStart.x - (touch.clientX || touch.pageX)
    const deltaY = touchStart.y - (touch.clientY || touch.pageY)

    onScroll(-deltaY * .3)
    touchStart.x =  (touch.clientX || touch.pageX)
    touchStart.y = (touch.clientY || touch.pageY)

    
})
canvas.addEventListener('wheel', e => onScroll(e.deltaY))
// Updating on mousemove/touchmove
function onMove() {
    
    const pos = getMousePos()
    pos.z = camera.position.z - 190
    point.position.copy(pos)

    bigDiamond.children.map((c,i) => {c.rotation.x = i * pos.y * .05; c.rotation.y = i * pos.x * .05})

    gsap.to(bigDiamond.rotation, {z: pos.z * .01, x: pos.x * .01})
    const tp = {...point.position, z: point.position.z - 400}
    
    if (whale) gsap.to(whale.position, 4, tp)
    if (whale) whale.lookAt(point.position)
}


// Updating on scroll

function onScroll(deltaY) {
    let whaleDuration = 3
    if (uniforms.uScroll.value < -1000 ) {
        gsap.to(camera.position, isMobile ? 0 : .1, {z: '-=' + (isMobile ? deltaY * 5 : deltaY)})
        gsap.to(point.position, isMobile ? 0 : .1, {z: '-=' + deltaY})
        if (deltaY > 0) whaleDuration = .5
    }
    else {
        gsap.to(camera.position, isMobile ? 0 : .1, {y: '+=' + deltaY})
        gsap.to(point.position, isMobile ? 0 : .1, {y: '+=' + deltaY})


    }

    camera.position.z = Math.max(camera.position.z, 200)



    if (camera.position.y < -640) gsap.to(camera.position, .1, {y: -640})
    uniforms.uScroll.value += deltaY
     gsap.to(bigDiamond.rotation, .1, {z: '+=' + (deltaY * .01)})
     gsap.to(diamondsMesh.rotation, .1, {z: '+=' + (deltaY * .01)})
     gsap.to(INTERACTIVE.rotation, .1, {y: '+=' + (deltaY * .01)})
     const tp = {...point.position, z: point.position.z - 400}
     if (whale) gsap.to(whale.position, whaleDuration, tp)
     if (whale) whale.lookAt(point.position)


}
// Mousemove
document.addEventListener('touchmove', (e) => {
    const touch = e.originalEvent?.touches[0] || e.originalEvent?.changedTouches[0] || e.touches[0] || e.changedTouches[0]
    mouse.x = ( (touch.clientX || touch.pageX) / window.innerWidth ) * 2 - 1
    mouse.y = - ( (touch.clientY || touch.pageY) / window.innerHeight ) * 2 + 1
    
})
document.addEventListener('mousemove', (e) => {

    //gsap.to(mouse, .1, {x: ( e.clientX / window.innerWidth ) * 2 - 1, y: - ( e.clientY / window.innerHeight ) * 2 + 1})
    mouse.x = ( e.clientX / window.innerWidth ) * 2 - 1
    mouse.y = - ( e.clientY / window.innerHeight ) * 2 + 1
    onMove()

})


const getMousePos = () => {
    var vector = new THREE.Vector3(mouse.x, mouse.y, 0)
    vector.unproject( camera );
    var dir = vector.sub( camera.position ).normalize();
    var distance = - camera.position.z / dir.z;
    distance = 200
    const pos = camera.position.clone().add( dir.multiplyScalar( distance ) );
    pos.y -= 10
    return pos
    
}

// Model
let oldElapsedTime = 0

scene.traverse((object) => {
   bufferScene.add(object.clone())
})



gsap.timeline()
    .from(I.position, 1, {y: '-=10'})
    .from(I.rotation, 1, {y: Math.PI / 2}, '<.1')
    .from(I.material, 1, {opacity: 0}, '<.1')
    .from(DEVELOP.position, 1, {y: '-=10'}, '<.3')
    .from(DEVELOP.rotation, 1, {x: Math.PI / 2}, '<')
    .from(DEVELOP.material, 1, {opacity: 0}, '<')


// Postprocessing

//COMPOSER

const composer = new EffectComposer(renderer);
const renderPass = new RenderPass(scene, camera);
composer.addPass(renderPass);



//custom shader pass

let counter = 0.0;
const myEffect = {
  uniforms: {
    "tDiffuse": { value: null },
    "amount": { value: counter }
  },
  vertexShader: postVertex,
  fragmentShader: postFragment
}

var customPass = new ShaderPass(myEffect);
customPass.renderToScreen = true;
composer.addPass(customPass);

// scene.children.map( c => c.visible = false)
let ci = 0
// setInterval(() => {scene.children[ci].visible = true; ci ++}, 4000)
const tick = () => {
    requestAnimationFrame(tick)
    const elapsedTime = clock.getElapsedTime()
    const delta = elapsedTime - oldElapsedTime
    oldElapsedTime = elapsedTime
    //controls.update()
    //cube.rotation.y = elapsedTime
    INTERACTIVE.rotation.y -= .01
    

    renderer.setRenderTarget(bufferTexture);
    renderer.render(scene, camera);
    renderer.setRenderTarget(null);

    if (whaleMixer) whaleMixer.update( delta * 2 );
    if (skullMixer) skullMixer.update( delta * 2 );

    diamondsMesh.rotation.z -= .005

    diamondsMesh.children.map(d => d.rotation.x = elapsedTime * .3 )
    pMaterial.uniforms.uTime.value = elapsedTime;
    particles.rotation.y = elapsedTime * .05
    clouds.map((c,i) => {
        c.position.x = Math.sin(elapsedTime * .2) * 200 - i
    })
    stats.update()
    uniforms.uTime.value = elapsedTime
    uniforms.uLightPos.value = new THREE.Vector3(point.position.x, point.position.y, point.position.z)
    Object.assign(uniforms.uCameraPos.value, camera.position)

    counter += 0.01;
    customPass.uniforms["amount"].value = counter;
    composer.render();
    leo && leo.children.map((m, i) =>  {
        m.rotation.x = elapsedTime * i * .05
        m.rotation.z = elapsedTime * i * .05
    })
   if (fisher) {
       fisher.position.y += Math.sin(elapsedTime * 2) * .1
       fisher.rotation.x += Math.sin(elapsedTime) * .003
   }
   if (skull) skull.lookAt(point.position)

   if (isMobile) bigDiamond.rotation.y = elapsedTime * .1

}   
gui.hide()
tick()
